@import url(https://fonts.googleapis.com/css2?family=Kanit:ital,wght@1,600;1,700;1,800;1,900&family=Varela+Round&display=swap);
body{background-color:#E7EB59;font-family:"Lato", sans-serif;margin:0;height:100vh;height:calc(1vh * 100);height:calc(var(--vh, 1vh) * 100)}input[type="text"],input[type="password"],input[type="submit"]{background-color:#000;color:#fff;padding:4px 13px;border:2px solid #000;border-radius:10px;outline:none;text-align:left;font-size:18px;cursor:text;transition:.2s}input[type="text"]:focus,input[type="password"]:focus,input[type="submit"]:focus{border-color:#FAB131;transition:.2s}ul{list-style-type:none;text-align:left}#root{height:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.app__container{-webkit-flex:1 1;flex:1 1;display:-webkit-flex;display:flex}

.header{font-family:"Kanit", sans-serif;font-style:italic;display:-webkit-flex;display:flex;-webkit-flex-grow:0;flex-grow:0;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;background-color:#E7EB59;height:65px;width:100%}.header__title{font-size:50px;color:black;font-weight:600;cursor:default;margin-left:30px}@media (max-height: 600px){.header__title{font-size:30px}}@media (max-width: 576px){.header__title{margin-left:10px;font-size:30px}}.header svg{width:45px;height:45px;margin-right:30px;cursor:pointer}@media (max-height: 600px){.header svg{width:30px;height:30px}}@media (max-width: 576px){.header svg{margin-right:10px;width:30px;height:30px}}

