.leftPanel{display:-webkit-flex;display:flex;-webkit-flex-grow:1;flex-grow:1;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:normal;justify-content:normal;-webkit-align-items:normal;align-items:normal;mask-image:linear-gradient(to top, transparent, black),linear-gradient(to right, transparent 11px, black 11px);mask-size:10000px 200000px;mask-repeat:no-repeat;mask-position:left bottom;-webkit-mask-image:linear-gradient(to top, transparent, black),linear-gradient(to right, transparent 11px, black 11px);-webkit-mask-size:10000px 200000px;-webkit-mask-repeat:no-repeat;-webkit-mask-position:left bottom;transition:mask-position 0.3s, -webkit-mask-position 0.3s;font-size:26px;max-height:calc(100vh - 65px);min-width:430px;z-index:1;cursor:default;overflow-y:auto;overflow-x:visible;direction:rtl;transition:.5s;padding-left:11px}.leftPanel::-webkit-scrollbar{width:10px}.leftPanel::-webkit-scrollbar-track{background-color:black}.leftPanel::-webkit-scrollbar-thumb{background-color:#FAB131}.leftPanel:hover{mask-position:left top;-webkit-mask-position:left top}@media (max-width: 768px){.leftPanel{min-width:120px}}@media (max-width: 576px){.leftPanel{min-width:70px}}.leftPanel__flipped{display:-webkit-flex;display:flex;-webkit-flex-grow:1;flex-grow:1;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:normal;justify-content:normal;-webkit-align-items:normal;align-items:normal;background-color:#282C34;direction:ltr}.leftPanel__foo{background-color:#E7EB59;-webkit-flex-grow:1;flex-grow:1}

.messages{background:#282C34;display:-webkit-flex;display:flex;-webkit-flex-grow:0;flex-grow:0;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:normal;align-items:normal;height:100%}.messages__container{display:-webkit-flex;display:flex;-webkit-flex-grow:0;flex-grow:0;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:normal;justify-content:normal;-webkit-align-items:normal;align-items:normal;mask-image:linear-gradient(to top, transparent, black),linear-gradient(to left, transparent 11px, black 11px);mask-size:10000px 200000px;mask-repeat:no-repeat;mask-position:right bottom;-webkit-mask-image:linear-gradient(to top, transparent, black),linear-gradient(to left, transparent 11px, black 11px);-webkit-mask-size:10000px 200000px;-webkit-mask-repeat:no-repeat;-webkit-mask-position:right bottom;transition:mask-position 0.3s, -webkit-mask-position 0.3s;overflow-y:auto}.messages__container::-webkit-scrollbar{width:10px}.messages__container::-webkit-scrollbar-track{background-color:black}.messages__container::-webkit-scrollbar-thumb{background-color:#FAB131}.messages__container:hover{mask-position:right top;-webkit-mask-position:right top}.messages__container__displayContent{display:-webkit-flex;display:flex;-webkit-flex-grow:0;flex-grow:0;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:normal;justify-content:normal;-webkit-align-items:normal;align-items:normal;width:100%;max-height:calc(100vh - 145px);padding-right:30px;overflow-y:visible}@media (max-width: 576px){.messages__container__displayContent{padding-right:16px}}.messages__singleMessage{position:relative;border-radius:20px;letter-spacing:1px;padding:10px 20px;margin:10px;max-width:60%;font-size:18px;cursor:default}@media (max-width: 576px){.messages__singleMessage{font-size:12px;padding:5px 10px;border-radius:10px}}.messages__singleMessage:hover>.messages__singleMessage__tooltip{opacity:100%;transition:opacity .2s linear 1s}.messages__singleMessage__tooltip{background:#FA6314;font-size:11px;position:absolute;top:10px;border-radius:5px;padding:8px;opacity:0;z-index:100}@media (max-width: 576px){.messages__singleMessage__tooltip{display:none}}.messages__singleMessage__tooltip::after{content:"";position:absolute;top:8px;border-width:5px;border-style:solid}.messages__singleMessage--receivedMsg{background:#FAB131;margin-left:10px;margin-right:auto;color:#fff}.messages__singleMessage--receivedMsg .messages__singleMessage__tooltip{left:calc(100% + 10px)}.messages__singleMessage--receivedMsg .messages__singleMessage__tooltip::after{right:100%;border-color:transparent #FA6314 transparent transparent}.messages__singleMessage--receivedMsg.messages__singleMessage--topSticky{margin-top:1px;border-top-left-radius:0}.messages__singleMessage--receivedMsg.messages__singleMessage--bottomSticky{margin-bottom:1px;border-bottom-left-radius:0}.messages__singleMessage--myMsg{background:#E7EB59;margin-right:10px;margin-left:auto;color:#000}.messages__singleMessage--myMsg .messages__singleMessage__tooltip{right:calc(100% + 10px)}.messages__singleMessage--myMsg .messages__singleMessage__tooltip::after{left:100%;border-color:transparent transparent transparent #FA6314}.messages__singleMessage--myMsg.messages__singleMessage--topSticky{margin-top:1px;border-top-right-radius:0}.messages__singleMessage--myMsg.messages__singleMessage--bottomSticky{margin-bottom:1px;border-bottom-right-radius:0}.messages__time{font-size:12px;text-align:center;color:#E7EB59;margin-top:15px}@media (max-width: 576px){.messages__time{font-size:8px}}

.newMessageInput{display:-webkit-flex;display:flex;-webkit-flex-grow:0;flex-grow:0;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:normal;justify-content:normal;-webkit-align-items:normal;align-items:normal}.newMessageInput__input{display:-webkit-flex;display:flex;-webkit-flex-grow:1;flex-grow:1;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:normal;justify-content:normal;-webkit-align-items:normal;align-items:normal;margin:18px}@media (max-width: 576px){.newMessageInput__input{margin:10px;font-size:12px !important}}.newMessageInput__btn{width:35px;height:35px;margin:20px 20px 20px 0;transition:.2s;cursor:pointer}@media (max-width: 576px){.newMessageInput__btn{width:20px;height:20px;margin:10px 10px 10px 0}}.newMessageInput__btn:hover{-webkit-filter:brightness(0%);filter:brightness(0%);width:45px}@media (max-width: 576px){.newMessageInput__btn:hover{width:25px}}

.seenAvatar{height:20px;width:20px;margin-left:-50px;border-radius:50%;position:absolute;z-index:4}@media (max-width: 576px){.seenAvatar{height:12px;width:12px;margin-left:-30px}}

.loginPage{background-color:#282C34;display:-webkit-flex;display:flex;-webkit-flex-grow:1;flex-grow:1;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.loginPage__title{font-family:"Kanit", sans-serif;font-size:30px;font-weight:600;color:#FAB131;padding-bottom:30px}@media (max-width: 576px){.loginPage__title{font-size:18px}}.loginPage__form{display:-webkit-flex;display:flex;-webkit-flex-grow:0;flex-grow:0;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:normal;justify-content:normal;-webkit-align-items:normal;align-items:normal;font-size:22px;font-family:"Kanit", sans-serif;color:white}.loginPage__label{background:#000;border-radius:10px;margin:0 auto 0 10px;padding:10px 20px;letter-spacing:1px}.loginPage__input{background:#000;font-family:"Kanit", sans-serif;font-size:22px !important;text-align:center !important;letter-spacing:1px;padding:10px 20px !important;margin:10px;border:none;outline:none}.loginPage__authFailed{font-size:18px;color:#FAB131;margin-top:20px;letter-spacing:1px}footer{background:#eee;font-size:13px;font-weight:lighter;color:#555;text-align:center;padding:5px;letter-spacing:1px}

