@import "./variables";
@import "./mixins";

.rightPanel {
    background: $backgroundColor;
    @include setFlex(column, 1);
    @include fancyScrollbar(right);
    max-height: calc(100vh - 125px);
    min-width: 350px;
    max-width: 350px;
    padding: 40px 20px 20px 40px;
    cursor: default;
    overflow-y: scroll;

    @media (max-width: 1200px) {
        display: none;
    }

    &__userAvatar {
        width: 200px;
        height: 200px;
        border: 5px solid $primaryColor;
    }
    
    &__nameSurname {
        font-weight: bold;
        font-size: 26px;
        margin-top: 40px;
        color: $primaryTextColor;
    }
    
    &__username {
        font-weight: lighter;
        font-size: 15px;
        color: $secondaryTextColor;
    }
}